@font-face {
  font-family: 'Poppins', sans-serif;
  src: url("assets/google-fonts/Poppins-Regular.ttf");
  src: url("assets/google-fonts/Poppins-Medium.ttf");
  src: url("assets/google-fonts/Poppins-MediumItalic.ttf");
  src: url("assets/google-fonts/Poppins-Italic.ttf");
  src: url("assets/google-fonts/Poppins-SemiBold.ttf");
  src: url("assets/google-fonts/Poppins-SemiBoldItalic.ttf");
  src: url("assets/google-fonts/Poppins-Bold.ttf");
  src: url("assets/google-fonts/Poppins-BoldItalic.ttf");
}

