.bottom-toolbar-margin {
  padding-bottom: 70px;
}

.imageClass {
  height: 70px;
  width: 100px;
}


.image-container {
  position: relative;
  height: 80px;
  width: 80px;

  .imageClass {
    height: 80px;
    width: 80px;
  }

  .fa-times {
    position: absolute;
    top: 0;
    right: 0;
  }
}

